<template>
	<!-- <Flow_nav_buttons @init_session="init_session()" @clear_session="clear_session()" @restore_session="restore_session" @status="status()" @next_step="next_step()" /> -->
	<small>{{ flow_heading }}</small>
	<div :class="[find_template]" v-if="find_template === 'review'">
		<div class="row">
			<Actions_loader v-for="(value, key) in step_actions" :action="value" @requested_step="next_step" />
		</div>
		<div class="row mt-5">
			<div class="col-3 offset-9">
				<button class="btn btn-primary btn-lg btn-right" @click="validate_forms()">
					Pokračovať
				</button>
			</div>
		</div>
	</div>
	<div :class="[find_template]" v-if="find_template === 'final'">
		<div class="row">
			<Actions_loader v-for="(value, key) in step_actions" :action="value" @requested_step="next_step" />
		</div>
	</div>
</template>

<script>
	import Flow_nav_buttons from '@/components/nav/Flow_nav_buttons.vue';
	import Flow_handling from '@/logic/requests/Flow_handling.js';
	import State from '@/store/State.js';
	import Actions_loader from '@/components/flow/Actions_loader.vue';
	import Get_form from '@/logic/requests/Get_form.js';

	export default {
		props: {
		},
		components: {
			Flow_nav_buttons,
			Actions_loader,
		},
		data() {
			return {
				status_repeat_frequency: 3 * 1000, // 3 seconds
				status_repeats_count: 0,
				status_repeats_limit: 10,
				// show_error: null,
				pending_actions: 0,
			}
		},
		methods: {
			/**
			 * init_session
			*/
			init_session()
			{
				// restore
				if (State.get_session_token() !== '') {
					this.status();
					return;
				}
				// new
				Flow_handling.flow_init_session().then(
					() => {
						this.what_to_do_next();
					}
				);
			},

			/**
			 * clear_session
			*/
			// clear_session()
			// {
			// 	State.set_session_token('');
			// 	State.clear_workflow_status();
			// 	State.clear_all_user_inputs();
			// },

			/**
			 * restore_session
			*/
			// restore_session(uuid)
			// {
			// 	if (uuid === '') {
			// 		return;
			// 	}
			// 	if (State.get_session_token() !== '') {
			// 		this.clear_session();
			// 	}
			// 	State.set_session_token(uuid);
			// 	this.status();
			// },

			/**
			 * validate_forms
			*/
			validate_forms()
			{
				this.pending_actions = 0;
				let actions = State.get_workflow_actions();

				for ( var key in actions ) {
					if ( ! actions.hasOwnProperty( key ) ) { continue; }

					if (actions[key].function !== 'getForm') {
						continue;
					}
					this.pending_actions++;

					Get_form.osmapi_validate_form(actions[key]).then(
						(response) => {
							State.set_form_errors(response.result.data.resultData.form_validation_error_elements);
							// if (response.result.data.resultData.validform === false) {
								// this.show_first_tab_with_error();
							// }
							if (response.result.data.resultData.validform !== false) {
								this.pending_actions--;
								if (this.pending_actions === 0) {
									this.next_step();
								}
							}
						}
					);

				}
			},

			/**
			 * next_step
			*/
			next_step(requested = undefined)
			{
				this.status_repeats_count = 0;
				Flow_handling.flow_next_step(requested).then(
					() => {
						this.what_to_do_next();
					}
				);
			},

			/**
			 * status
			*/
			status()
			{
				Flow_handling.flow_status().then(
					() => {
						this.what_to_do_next();
					}
				);
			},

			/**
			 * what_to_do_next
			*/
			what_to_do_next()
			{
				var state = State.get_workflow_step_state();
				var internal = State.get_workflow_internal();
				var finished = State.get_workflow_finished();
				var actions = State.get_workflow_actions();

				if (finished === true) {
					return;
				}
				else if (state === 'success') {
					this.next_step();
				}
				// else if (state === 'pending' && actions.length > 0 && internal === false) {
					// this.actions();
					// actions will be printed automatically
				// }
				else if (internal === true) {
					if (this.status_repeats_count > this.status_repeats_limit) {
						console.log('reached status repeat limit, stopping requests');
						return;
					}
					this.status_repeats_count++;
					setTimeout(
						function() {
							this.status();
						}.bind( this ),
						this.status_repeat_frequency
					);
				}
				// else if (state === 'error') {
				// 	this.show_error = 'session flow received error';
				// }
			},
		},
		computed: {
			flow_heading()
			{
				return State.get_session_token() + ' : ' + State.get_workflow_step() + ' / ' + State.get_workflow_step_state() +
				( (State.get_workflow_internal()) ? ' / internal' : '' ) + ( (State.get_workflow_finished()) ? ' / session finished' : '' );
			},
			step_actions()
			{
				var all_actions = State.get_workflow_actions();
				var without_template = {};
				var i, len = all_actions.length;
				for ( i = 0; i < len; i++ ) {
					if (all_actions[i].function !== 'template') {
						without_template[all_actions[i].functionPosition.order] = all_actions[i];
					}
				}
				return without_template;
			},
			find_template()
			{
				var actions = State.get_workflow_actions();
				var i, len = actions.length;
				for ( i = 0; i < len; i++ ) {
					if (actions[i].function === 'template') {
						return actions[i].functionParams.type;
					}
				}
				return null;
			},
		},
		created() {
			this.init_session();
		},
	}
</script>

<style scoped lang="scss">
	// template names
	.review {
		background-color: #eef9fb;
		padding: 16px;
	}
	.final {
		background-color: #3cb371;
	}
</style>
