	<div class="row">
		<div class="col-12">
			<Tabs :active_tab="active_tab" :tab_current="tab_current" :errors="errors" />

			<!-- <div class="text-right mt-4">
				<button class="btn btn-primary btn-lg btn-right" @click="validate()">
					Pokračovať
				</button>
