<template>
	<div :class="[size]">
		<Image_action v-if="type === 'image'" :action="action" />
		<Navigation_action v-else-if="type === 'navigation'" :action="action" @requested_step="requested_step" />
		<Get_form_action v-else-if="type === 'getForm'" :action="action" />
		<Get_html_action v-else-if="type === 'getHtml'" :action="action" />
	</div>
</template>

<script>
	import Image_action from '@/components/flow/actions/Image_action.vue';
	import Navigation_action from '@/components/flow/actions/Navigation_action.vue';
	import Get_form_action from '@/components/flow/actions/Get_form_action.vue';
	import Get_html_action from '@/components/flow/actions/Get_html_action.vue';

	export default {
		props: {
			action: Object,
		},
		components: {
			Image_action,
			Navigation_action,
			Get_form_action,
			Get_html_action,
		},
		data() {
			return {
			}
		},
		mounted() {
		},
		emits: ['requested_step'],
		methods: {
			requested_step(int) {
				this.$emit('requested_step', int);
			},
		},
		computed: {
			type() {
				return this.action.function;
			},
			size() {
				if (this.action.functionPosition.size === 'full') {
					return 'col-12';
				} else if (this.action.functionPosition.size === 'half') {
					return 'col-12 col-lg-6';
				} else {
					return 'col-12';
				}
			},
		},
	}
</script>
